<template>
  <page
    @on-ready="onReady"
    @on-show="onShow"
    ref="page"
    class="plan-record-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar slot="header" id="header" title="记录故障" left-arrow @click-left="onClickLeft">
    </van-nav-bar>

    <van-cell-group>
      <van-cell required title="故障设备" is-link clickable @click="handleSelectDevice"
        >{{ deviceName ? deviceName : '请选择' }}</van-cell
      >
      <van-field v-model="deviceNo" placeholder="设备序列号" type="textarea" :rows="1" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-field v-model="remark" placeholder="备注" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <div class="blank-empty"></div>

    <template slot="blank">
      <div class="footer-action">
        <div class="action-item" @click="handleRemove" v-if="removeShow">
          <i class="action-item-icon icon-shanchu"></i>
          <span>删除</span>
        </div>
        <div class="action-item" @click="handleCancel">
          <i class="action-item-icon icon-quxiao"></i>
          <span>取消</span>
        </div>
        <van-button @click="handleSave" class="action-item-main" color="#108EE9">确认</van-button>
      </div>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'
// import io from '../../api'
import db from '../../db'
import native from '../../utils/native.js'
import {statusEnum, formStatusEnum} from '../../utils/enum.js'

export default {
  components: {
    Page,
  },
  data: () => ({
    headerHeightVW: 0,
    deviceId: '',
    deviceName: '',
    deviceNo: '',
    remark: '',
    id: '',
    index: null,
    editIndex: -1,
    removeShow: false
  }),
  methods: {
    onReady (header, main) {
      this.header = header

      this.headerHeightVW = (this.header.h + 1) * 100 / this.header.w

      this.id = navigate.getParam('id')

      this.index = navigate.getParam('index')

      this.deviceId = navigate.getParam('deviceId')

      this.deviceName = navigate.getParam('deviceName')

      this.deviceNo = navigate.getParam('deviceNo')

      this.remark = navigate.getParam('remark')

      this.editIndex = navigate.getParam('editIndex')

      console.log(this.editIndex)

      if (this.editIndex >= 0) {
        this.removeShow = true
      }

    },
    onShow () {
      if (localStorage.getItem('device_select')) {
        const device = JSON.parse(localStorage.getItem('device_select'))

        this.deviceId = device.id

        this.deviceName = device.name

        this.deviceNo = device.serial

        localStorage.setItem('device_select', '')
      } else {
        // this.device = null
      }
    },
    onClickLeft () {
      navigate.back()
    },

    handleRemove () {
      db.getOrderDetail(this.id).then(data => {

        if (this.editIndex >= 0) {
          data.formList[this.index].faultList.splice(this.editIndex, 1)
        }

        db.updateOrder(data).then(() => {
          navigate.back()
        })
      })
    },

    handleSave () {
      if (!this.deviceId) {
        this.$toast('请选择设备')
        return false
      }

      db.getOrderDetail(this.id).then(data => {
        console.log(JSON.stringify(data))

        if (this.editIndex) {
          data.formList[this.index].faultList[this.editIndex] = {
            deviceId: this.deviceId,
            deviceName: this.deviceName,
            deviceNo: this.deviceNo,
            remark: this.remark
          }
        } else {
          if (data.formList[this.index].faultList) {
            data.formList[this.index].faultList.push({
              deviceId: this.deviceId,
              deviceName: this.deviceName,
              deviceNo: this.deviceNo,
              remark: this.remark
            })
          } else {
            data.formList[this.index].faultList = [{
              deviceId: this.deviceId,
              deviceName: this.deviceName,
              deviceNo: this.deviceNo,
              remark: this.remark
            }]
          }
        }

        db.updateOrder(data).then(() => {
          navigate.back()
        })
      })
    },

    handleSelectDevice () {
      navigate.open('device', {category: '1'})
    },

    handleCancel () {
      navigate.back()
    },
  }
}
</script>

